
let wasm;

/**
* @param {number} tx
* @param {number} ty
* @param {number} rotation
* @param {number} scale_x
* @param {number} scale_y
* @param {number} skew_x
* @param {number} skew_y
* @param {number} x
* @param {number} y
* @param {number} right
* @param {number} bottom
*/
export function load_matrix(tx, ty, rotation, scale_x, scale_y, skew_x, skew_y, x, y, right, bottom) {
    wasm.load_matrix(tx, ty, rotation, scale_x, scale_y, skew_x, skew_y, x, y, right, bottom);
}

/**
* @returns {number}
*/
export function get_quad_pointer() {
    var ret = wasm.get_quad_pointer();
    return ret;
}

/**
* @returns {number}
*/
export function get_bounds_pointer() {
    var ret = wasm.get_bounds_pointer();
    return ret;
}

async function load(module, imports) {
    if (typeof Response === 'function' && module instanceof Response) {
        if (typeof WebAssembly.instantiateStreaming === 'function') {
            try {
                return await WebAssembly.instantiateStreaming(module, imports);

            } catch (e) {
                if (module.headers.get('Content-Type') != 'application/wasm') {
                    console.warn("`WebAssembly.instantiateStreaming` failed because your server does not serve wasm with `application/wasm` MIME type. Falling back to `WebAssembly.instantiate` which is slower. Original error:\n", e);

                } else {
                    throw e;
                }
            }
        }

        const bytes = await module.arrayBuffer();
        return await WebAssembly.instantiate(bytes, imports);

    } else {
        const instance = await WebAssembly.instantiate(module, imports);

        if (instance instanceof WebAssembly.Instance) {
            return { instance, module };

        } else {
            return instance;
        }
    }
}

async function init(input) {
    if (typeof input === 'undefined') {
        input = new URL('rustwasm_bg.wasm', import.meta.url);
    }
    const imports = {};


    if (typeof input === 'string' || (typeof Request === 'function' && input instanceof Request) || (typeof URL === 'function' && input instanceof URL)) {
        input = fetch(input);
    }



    const { instance, module } = await load(await input, imports);

    wasm = instance.exports;
    init.__wbindgen_wasm_module = module;

    return wasm;
}

export default init;

